import Vue from 'vue'
import '../../dist/template-2.css'
import SimpleCrop from '../../index.vue';

new Vue({
  el: '#app',
  data: {
    cropParams: {
      src: "../../img/test2.jpg",
      size: {
        width: 800,
        height: 600
      },
      borderColor: '#fff',
      cropSizePercent: 0.9
    }
  },
  components: {
    'simple-crop': SimpleCrop
  },
  methods: {
    //
    updateComponent() {
      this.cropParams = JSON.parse(JSON.stringify(this.cropParams)); //
      this.cropParams.borderColor = '#0BFF00'; //
      this.cropParams.cropSizePercent = 0.8;
      this.cropParams.size = {
        width: 600,
        height: 600
      };
    },

    //
    setCropImage(evt) {
      var files = evt.target.files;
      if (files.length > 0) {
        this.cropParams = JSON.parse(JSON.stringify(this.cropParams)); //
        this.cropParams.src = files[0];
      }
      evt.target.value = '';
    },

    //
    closeCallback() {
      console.log('closeCallback');
    },

    //
    cropCallback($resultCanvas) {
      console.log('cropCallback');
      $resultCanvas.style.marginRight = "10px";
      $resultCanvas.style.width = "50%";
      document.body.appendChild($resultCanvas);
    },

    //
    uploadCallback(src) {
      console.log('uploadCallback ' + src);
    }
  }
});